@import "https://fonts.googleapis.com/css2?family=Fraunces:opsz,wght@9..144,700&family=Montserrat:wght@500;700&display=swap";
@import "https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css";
mark::before, mark::after,
del::before,
del::after,
s::before,
s::after {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  white-space: nowrap;
}

mark,
del,
s {
  position: relative;
}
s::before {
  content: " [strike-through start] ";
}

s::after {
  content: " [strike-through end] ";
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: montserrat, sans-serif;
  color: hsl(228, 12%, 48%);
  background-color: hsl(30, 38%, 92%);
}

main {
  display: flex;
  place-items: center center;
  justify-content: center;
  height: 100vh;
  font-size: 0.875rem;
}

.product-card {
  display: flex;
  flex-direction: column;
  max-width: 22rem;
  background-color: hsl(0, 0%, 100%);
  border-radius: 10px;
}
@media (width >= 60rem) {
  .product-card {
    flex-direction: row;
    max-width: 40rem;
    height: 25rem;
  }
}
.product-card__img {
  width: 100%;
  border-radius: 10px 10px 0 0;
}
@media (width >= 60rem) {
  .product-card__img {
    width: unset;
    height: 100%;
    border-radius: 10px 0 0 10px;
  }
}
.product-card__ribbon {
  margin-block: 1rem;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.5rem;
}
.product-card__title {
  margin-block: 1rem;
  font-family: fraunces, serif;
  font-size: 2rem;
  color: hsl(212, 21%, 14%);
  text-wrap: balance;
}
.product-card__text-half {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
@media (width >= 60rem) {
  .product-card__text-half {
    justify-content: space-between;
    margin-top: 1rem;
  }
}
.product-card__text-half > * {
  margin-block: 1rem 0;
  margin-inline: 1rem;
  text-wrap: balance;
}
@media (width >= 60rem) {
  .product-card__text-half > * {
    margin-inline: 2rem;
  }
}
.product-card__prices {
  display: flex;
  gap: 1rem;
  align-items: center;
}
.product-card__prices s {
  font-size: 0.9rem;
}
@media (width >= 60rem) {
  .product-card__prices {
    gap: 1.5rem;
  }
}
.product-card__price {
  font-family: fraunces, serif;
  font-size: 2rem;
  color: hsl(158, 36%, 37%);
}
.product-card__btn {
  display: flex;
  gap: 1rem;
  align-self: center;
  justify-content: center;
  width: 75%;
  padding: 1rem;
  margin-bottom: 1.2rem;
  font-weight: bold;
  color: hsl(0, 0%, 100%);
  background-color: hsl(158, 36%, 37%);
  border: 0;
  border-radius: 10px;
  transition: 250ms ease-in-out;
}
.product-card__btn:hover, .product-card__btn:focus {
  cursor: pointer;
  background-color: #1a4031;
}

footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  margin-bottom: 1rem;
  text-align: center;
}

.attribution {
  font-size: 0.6875rem;
  text-align: center;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}